.stlyleto{
    position: relative;
    top:10vw;
    align-items: center;
}
.pro-container{
    margin: 8px;
    direction: ltr;
    display: flex;
    justify-content:center;
    align-items: center;
    padding: 20px 5px 50px 5px;
    height: 12vw;
    border: solid 2px black;
    background-color: white;
    border-radius: 5px;
    
}
.propropart0{
    position: absolute;
    width:8vw;
    height: 8vw;
    left:-8vw;
    display: none;
    background-size:contain;
    background-repeat: no-repeat;
    transition-property: transform,left,display;
    transition-duration: 1s;
    text-align: center;
    cursor: pointer;

}
