.subtitle-slid{
    font-size: large;
    text-align: center;
    margin-top: 9vw;
    height: 50px;

}
.slide-container{
    width:100%;
    position: relative;
    direction: ltr;
    display: flex;
    align-items: center;
    padding:0;
    height: 20vw;
    top:45px;
    
}
.slidepart0{
    position: absolute;
    width:8vw;
    height: 8vw;
    left:-8vw;
    display: none;
    background-size: 8vw 8vw;
    background-repeat: no-repeat;
    transition-property: transform,left,display;
    transition-duration: 1s;

}
.slidepart1{
    position: absolute;
    width:15vw;
    height: 15vw;
    transform: scale(0.6);
    left:3vw;
    background-size: 15vw 15vw;
    background-repeat: no-repeat;
    transition-property: transform,left,display;
    transition-duration: 1s;

}

