.socalicons{
    position: fixed;
    top:0px;
    left: 0px;
    display: flex;
    align-items:center;
    background-color: blue;
    height: 60px;
    width: 100%;
    z-index: 300;
    margin: 0px;
    padding: 0px;
}
.social{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin-left: 5px;
    cursor: pointer;
    z-index: 300;

}
.logo{
    position: fixed;
    left: 45%;
    top:5px;
    transform: translate(-50%, 0);
    z-index: 300;
    font-size: xx-large;
    border-radius: 50% ;
    color: white;

}
.homepag{
    position: fixed;
	display: flex;
	justify-content: left;
	color: #fff;
	font-size: medium;
	direction: rtl;
	align-items: center;
    right: 1%;
	
}
.homepagex{
	margin-left: 10px;
	padding-bottom: 4px;
	background-color: #ffffff4a;
	border-radius: 5px;
	padding: 5px;
	text-decoration: none;
	color: white;
    padding: 5px 15px;
}
.homepagex:hover{
	margin-left: 10px;
	padding-bottom: 4px;
	background-color: #ffffff4a;
	border-radius: 5px;
	padding: 5px;
	text-decoration: none;
	color: rgb(3, 3, 3);
    padding: 5px 15px;
    cursor: pointer;
}