.h_iframe-aparat_embed_frame{
    position:relative;
}
.h_iframe-aparat_embed_frame .ratio{
    display:block;
    width:100%;
    height:auto;
}
.h_iframe-aparat_embed_frame iframe{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
}
.prslide-container{
    margin: 8px;
    direction: ltr;
    display: flex;
    justify-content:center;
    align-items: center;
    padding: 20px 5px 50px 5px;
    height: 12vw;
    border: solid 2px black;
    background-color: white;
    border-radius: 5px;
    
}
.prslidepart0{
    width:18vw;
    height: 13vw;
    background-size:18vw 13vw;
    background-repeat: no-repeat;
    transition-property: transform,left,display;
    transition-duration: 1s;
    border-radius: 10px;
    border: solid 2px transparent;
    margin: auto 10px;

}
.prslidepart0:hover{
    border: solid 2px rgb(0, 0, 0);
    cursor: pointer;
}
